.row {
    margin: 0 1em;
}

.img-fluid {
    max-width: 100%;
    height: auto;
}

.swiper-slide {
    width: auto!important;
}

:root {
    --swiper-theme-color: #fff;
}

@media screen and (max-width: 500px) {
    .swiper-button-next, .swiper-button-prev {
        display: none;
    }
}