.landing {
  position: relative;
}

.hero {
  height: 100vh;
  position: relative;
}

.hero-image-wrapper {
  height: 100%;
  position: relative;
}

.fluid-img {
  height: 100%;
  object-fit: cover;
}

.hero-img-gradient {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, .4);
  background-image: linear-gradient(
    to top,
    rgba(0, 0, 0, .8) 0,
    rgba(0, 0, 0, 0) 60%,
    rgba(0, 0, 0, .8) 100%
  );
}

.hero-content-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  color: white;
  max-width: 50em;
  margin: 0 auto;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1em;
}

.welcome-text {
  font-size: 1.75rem;
  font-weight: 600;
}

.hero-title {
  font-size: 4rem;
}

header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  padding: 0.5rem 2rem;
}

.logo > img {
  max-width: 13em;
}

.hero__form {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

.hero__form input {
  vertical-align: middle;
  margin: 5px 0px 5px 0;
  padding: 1em;
  background-color: #fff;
  border: 1px solid #ddd;
  font-size: 1rem;
}

.hero__form input:focus-visible {
    outline: 0;
}

.hero__form button {
  padding: 1em 2em;
  background-color: #e50914;
  border: 1px solid #e50914;
  color: white;
  font-size: 1rem;
  cursor: pointer;
}

@media screen and (max-width: 767px) {
  .welcome-text {
    font-size: 1.45rem;
    font-weight: 600;
  }

  .hero-title {
    font-size: 2.85rem;
  }

}

@media screen and (max-width: 500px) {
  .welcome-text {
    font-size: 1rem;
    font-weight: 600;
  }

  .hero-title {
    font-size: 1.85rem;
  }

  .hero__form input {
      font-size: 0.75rem;
      padding: .85em;
  }
  .hero__form button {
      font-size: 0.75rem;
      padding: .85em 2.5em;
  }
}
